___CSS_LOADER_EXPORT___.push([module.id, `header {
  /* background-color: aliceblue; */
  background-color: white;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 100px;
  display: flex;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  box-shadow: 0px 1px 5px 2px rgb(217, 217, 217);
  z-index: 100;
}

header .left {
  width: 40%;
  display: flex;
  align-items: center;
  /* justify-content: space-evenly; */
}

.name {
  font-size: 1.3rem;
  font-weight: 400;
  margin-left: 40px;
  cursor: pointer;
}

header .right {
  flex: 1;
}

header nav {
  display: flex;
  justify-content: space-between;
}

header nav button {
  font-weight: 300;
  font-size: 1rem;
}

header nav button:hover {
  color: hsl(209, 100%, 17%);
}
