___CSS_LOADER_EXPORT___.push([module.id, `.content {
  margin-top: 150px;
}

.banner {
  /* background-color: var(--banner-color); */
  background-color: aliceblue;
  height: 450px;
  width: 75%;
  min-width: 500px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  position: relative;
}

.banner .left {
  width: 60%;
  height: 100%;
}

.menu1 {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.banner .right {
  flex: 1;
  margin-left: 20px;
  padding-left: 10px;
  padding-right: 10px;
}

.banner .right p {
  font-size: 1.12rem;
}

.arrow {
  position: absolute;
  bottom: 3%;
  right: 3%;
  background-color: white;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.fa-arrow-right {
  font-size: 1.6rem;
  color: var(--hover-color);
}

.fa-arrow-right:hover {
  color: hsl(209, 90%, 27%);
}

.dish > .top {
  overflow: hidden;
  transition: 2s ease all;
  border-radius: 8px;
}

.dish img {
  border-radius: 8px;
  width: 100%;
  height: 270px;
  transition: 2s ease all;
  cursor: pointer;
}

.dish img:hover {
  transform: scale(1.1);
}

.title h3 {
  font-size: 1.4rem;
  font-weight: 400;
  margin: 0;
  display: inline;
  cursor: pointer;
}

h3:hover {
  color: lightsalmon;
}

.fa-star {
  color: orange;
  font-size: 1.1rem;
  margin-top: 10px;
  margin-bottom: 10px;
}


